<template>
  <div>
    <!-- loading -->
    <loading :active.sync="isLoading"></loading>
    <!--  -->
    <div class="text-right">
      <button class="btn btn-primary" @click="openModal(true)"></button>
    </div>
    <!--  -->
    <table class="table mt-4">
      <thead>
        <tr>
          <th width="100"></th>
          <th></th>
          <th width="80"></th>
          <th width="80"></th>
          <th width="100"></th>
          <th width="100"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="(item) in products" :key="item.id">
          <td>{{ item.category }}</td>
          <td> {{item.title}} </td>
          <td class="text-left">
            {{item.origin_price | currency}}
          </td>
          <td class="text-left">
            {{item.price | currency}}
          </td>
          <td>
            <!-- //is_enabled? -->
            <span class="text-success" v-if="item.is_enabled"></span>
            <span v-else></span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm" @click="openModal(false,item)"></button>
            <button class="btn btn-outline-primary btn-sm" @click="deletItem(item)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <!--  -->
    <Switchpages :pages='pagination' @switch="getProducts"></Switchpages>
    <!-- modal -->
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-4">
                <div class="form-group">
                  <label for="image"></label>
                  <input type="text" class="form-control" id="image"
                    v-model="tempProduct.imageUrl"
                    placeholder="">
                </div>
                <div class="form-group">
                  <label for="customFile"> 
                    <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
                  </label>
                  <!-- @change  -->
                  <input type="file" id="customFile" class="form-control"
                    ref="files" @change="uploadFile">
                </div>
                <img :src="tempProduct.imageUrl" img="https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format&fit=crop&w=1350&q=80"
                  class="img-fluid" alt="">
              </div>
              <div class="col-sm-8">
                <div class="form-group">
                  <label for="title"></label>
                  <input type="text" class="form-control" id="title"
                    placeholder="" v-model="tempProduct.title">
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                    <label for="category"></label>
                    <input type="text" class="form-control" id="category"
                      placeholder="" v-model="tempProduct.category">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input type="unit" class="form-control" id="unit"
                      placeholder="" v-model="tempProduct.unit">
                  </div>
                </div>

                <div class="form-row">
                  <div class="form-group col-md-6">
                  <label for="origin_price"></label>
                    <input type="number" class="form-control" id="origin_price"
                      placeholder="" v-model="tempProduct.origin_price">
                  </div>
                  <div class="form-group col-md-6">
                    <label for="price"></label>
                    <input type="number" class="form-control" id="price"
                      placeholder="" v-model="tempProduct.price">
                  </div>
                </div>
                <hr>

                <div class="form-group">
                  <label for="description"></label>
                  <textarea type="text" class="form-control" id="description"
                    placeholder="" v-model="tempProduct.description"></textarea>
                </div>
                <div class="form-group">
                  <label for="content"></label>
                  <textarea type="text" class="form-control" id="content"
                    placeholder="" v-model="tempProduct.content"></textarea>
                </div>
                <div class="form-group">
                  <div class="form-check">
                    <input class="form-check-input" type="checkbox"
                      id="is_enabled"
                      :true-value="1"
                      :false-value="0" 
                      v-model="tempProduct.is_enabled">
                    <label class="form-check-label" for="is_enabled">
                      
                    </label>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="updateProduct"></button>
          </div>
        </div>
      </div>
    </div>
    <!-- modal -->
    <div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel">
              <span></span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
             <strong class="text-danger">{{ tempProduct.title }}</strong> ()
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger sureDelete" @click="deletItemSure"
              ></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>   

<script>
//
import $ from 'jquery'
import Switchpages from "@/components/Switchpages"

//
export default {
  components: {
    Switchpages
  },
  data(){
    return{
      //
      pagination: {},
      //
      products:[],
      //Modal
      tempProduct:{},
      //Modal
      isNew: false,
      isLoading: false,
      //loading
      status: {
        fileUploading: false
      }
    }
  },
  methods: {
    //page = 1
    getProducts(page = 1){
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/products?page=${page}`
      const vm = this
      vm.isLoading = true
      this.$http.get(api).then((response)=>{
        console.log(response.data)
        vm.isLoading = false 
        vm.products = response.data.products
        vm.pagination = response.data.pagination
      })
    },
    //item
    deletItem(item){
      this.tempProduct = item
      $("#delProductModal").modal("show")
    },
    deletItemSure(){
      const vm = this
      const deletapi = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${vm.tempProduct.id}`
      this.$http.delete(deletapi).then((response)=>{
        console.log(response)
        if(response.data.success){
          $("#delProductModal").modal("hide")
          vm.getProducts()
        }else{
          $("#delProductModal").modal("hide")
          vm.getProducts()
          console.log("")
        }
      })        
    },
    //""""
    openModal(isNew,item){
      console.log("item",item)
      //
      if(isNew){
        this.tempProduct = {}
        this.isNew = true
        //
      } else {
        //()
        // this.tempProduct = item
        //item(ES6)
        this.tempProduct = Object.assign({},item)
        this.isNew = false
      }
      $("#productModal").modal('show')
    },
    //modal
    updateProduct(){
      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product`
      //post
      let httpMethod = 'post'
      const vm = this
      //()
      if(!vm.isNew){
        api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${vm.tempProduct.id}`
        //apiput
        httpMethod = 'put'
      }
      //tempProduct
      //datavm.tempProduct(/)
      this.$http[httpMethod](api,{ data: vm.tempProduct }).then((response)=>{
        console.log(response.data)
        if(response.data.success){
          $("#productModal").modal('hide')
          vm.getProducts()
        }else{
          $("#productModal").modal('hide')
          vm.getProducts()
          console.log('')          
        }
      })
      // this.$http.delete(api).then((response)=>{
      //   console.log(response)
      // })     
    },
    uploadFile(){
      console.log(this)
      //
      const uploadedFile = this.$refs.files.files[0]
      const vm = this
      vm.status.fileUploading = true
      //FormData web api 
      //https://developer.mozilla.org/zh-TW/docs/Web/API/FormData
      const formData = new FormData()
      //,
      //file-to-upload <input type="file" name="file-to-upload"> 
      formData.append('file-to-upload',uploadedFile)
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/upload`
      this.$http.post(url,formData,{
        // Content-type  (header) 
        //
        // form-data
        headers: {
          'Content-Type': 'multipart/form-data'
        }
      }).then((response)=>{
        //loading
        vm.status.fileUploading = false
        console.log(response.data)
        //
        // vm.tempProduct.imageUrl = response.data.imageUrl
        // console.log(vm.tempProduct)
        //$set(,,)
        if(response.data.success){
          vm.$set(vm.tempProduct, 'imageUrl', response.data.imageUrl)
          console.log(vm.tempProduct)
        } else {
          this.$bus.$emit('message:push',response.data.message,'danger')
        }
      })
    }
  },
  created() {
    this.getProducts()
    // this.$bus.$emit('message:push','','warning')
  }
}
</script>