//github

import Vue from 'vue'
import VueRouter from 'vue-router'

//frontend
import Home from '@/views/frontend/Home'
import Shop from '@/views/frontend/Shop'
import Login from '@/views/frontend/Login'
import FrontOrders from '@/views/frontend/FrontOrders'
import FrontCheckout from '@/views/frontend/FrontCheckout'
import Favorite from '@/views/frontend/Favorite'
import Coupon from '@/views/frontend/Coupon'

//backend
import Dashboard from '@/views/backend/Dashboard'
import Products from '@/views/backend/Products'
import CustomerOrders from '@/views/backend/CustomerOrders'
import CustomerCheckout from '@/views/backend/CustomerCheckout'
import Orders from '@/views/backend/Orders'
import Cupons from '@/views/backend/Cupons'


Vue.use(VueRouter);
export default new VueRouter({
  routes: [
    //-----------------------------------------------------------
    {
      //
      name:'Home',
      //()
      path:'/home',
      //
      component: Home,
    },
    {
      //()login
      path: '*',
      redirect: '/home'
    },
    {
      //
      name:'Shop',
      //()
      path:'/shop',
      //
      component: Shop,
    },
    {
      name:'FrontOrders',
      path: '/front_orders',
      component: FrontOrders
    },
    {
      path: '/front_checkout/:orderId',
      name: 'FrontCheckout',
      component: FrontCheckout,         
    },
    {
      path: '/login',
      name:'Login',
      component: Login
    },
    {
      path: '/favorite',
      name: 'Favorite',
      component: Favorite, 
    },
    {
      //
      name:'Coupon',
      //()
      path:'/coupon',
      //
      component: Coupon,
    },
    //------------------------------------------------
    {
      path: '/admin',
      name:'Dashboard',
      component: Dashboard,
      children: [
        {
          //"/"
          path: 'products',
          name: 'Products',
          component: Products,
          //
          meta: { requiresAuth : true },          
        },
        {
          //"/"
          path: 'cupons',
          name: 'Cupons',
          component: Cupons,
          meta: { requiresAuth : true },           
        },
        {
          //"/"
          path: 'orders',
          name: 'Orders',
          component: Orders,
          meta: { requiresAuth : true },           
        },
      ]
    },
    //------------------------------------------
    {
      path: '/simulation',
      name:'Dashboard2',
      component: Dashboard,
      children: [
        {
          //"/"
          path: 'customer_orders',
          name: 'CustomerOrders',
          component: CustomerOrders,         
        },
        {
          //"/"
          path: 'customer_checkout/:orderId',
          name: 'CustomerCheckout',
          component: CustomerCheckout,         
        },
      ]    
    },
  ]
})


