<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="row mt-4">
      <div class="col-md-4 mb-4" v-for="item in products" :key="item.id">
        <div class="card border-0 shadow-sm">
          <div style="height: 150px; background-size: cover; background-position: center"
            :style="{backgroundImage: `url(${item.imageUrl})`}">
          </div>
          <div class="card-body">
            <span class="badge badge-secondary float-right ml-2">{{ item.category }}</span>
            <h5 class="card-title">
              <a href="#" class="text-dark">{{ item.title }}</a>
            </h5>
            <p class="card-text"></p>
            <div class="d-flex justify-content-between align-items-baseline">
              <!--  -->
              <del class="h6" v-if="!item.price">:{{ item.origin_price }}</del>
              <div class="h5" v-if="item.price">:{{ item.origin_price }}</div>
              <div class="h5" v-if="item.price">:{{ item.price }}</div>
            </div>
          </div>
          <div class="card-footer d-flex">
            <!-- //v-if -->
            <button type="button" class="btn btn-outline-secondary btn-sm" 
            @click="getProduct(item.id)">
              <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
              
            </button>
            <button type="button" class="btn btn-outline-danger btn-sm ml-auto" @click="addToCart(item.id)">
              <i class="fas fa-spinner fa-spin" v-if="status.loadingItem === item.id"></i>
              
            </button>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="modal fade" id="productModal" tabindex="-1" role="dialog"
      aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header">
            <h5 class="modal-title" id="exampleModalLabel">{{ product.title }}</h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <img :src="product.imageUrl" class="img-fluid" alt="">
            <blockquote class="blockquote mt-3">
              <p class="mb-0">{{ product.content }}</p>
              <footer class="blockquote-footer text-right">{{ product.description }}</footer>
            </blockquote>
            <div class="d-flex justify-content-between align-items-baseline">
              <div class="h4" v-if="!product.price">{{ product.origin_price }} </div>
              <del class="h6" v-if="product.price"> {{ product.origin_price }} </del>
              <div class="h4" v-if="product.price"> {{ product.price }} </div>
            </div>
            <select name="" class="form-control mt-3" v-model="product.num">
              <option :value="num" v-for="num in 10" :key="num">
                 {{num}} {{product.unit}}
              </option>
            </select>
          </div>
          <div class="modal-footer">
            <div class="text-muted text-nowrap mr-3">
               <strong>{{ product.num * product.price }}</strong> 
            </div>
            <button type="button" class="btn btn-primary"
              @click="addToCart(product.id,product.num)">
              <i class="fas fa-spinner fa-spin"></i>
              
            </button>
          </div>
        </div>
      </div>
    </div>
    <div class="row my-5 justify-content-center">
      <div class="row my-5 justify-content-center">
        <table class="table">
          <thead>
            <th></th>
            <th></th>
            <th></th>
            <th></th>
          </thead>
          <tbody>
            <!--  -->
            <tr v-for="item in cart.carts" :key="item.id">
              <td>
                <button class="btn" @click="removeCartItem(item.id)"><i class="fas fa-trash"></i></button>
              </td>
              <td>
                {{ item.product.title }}
                <div class="text-success" v-if="item.coupon">
                  
                </div>
              </td>
              <td>{{ item.qty }}/{{item.product.unit}}</td>
              <td>{{ item.product.price }}</td>
            </tr>
          </tbody>
          <tfoot>
            <tr>
              <td class="text-left"></td>
              <td class="text-right" colspan="3">{{cart.total}}</td>
            </tr>
            <tr v-if="cart.final_total !== cart.total">
              <td class="text-left text-success"></td>
              <td class="text-right text-success" colspan="3">{{cart.final_total}}</td>
            </tr>
          </tfoot>
        </table>
        <div class="input-group mb-3 input-group-sm" >
          <input type="text" class="form-control" placeholder="" v-model="cupon_code">
          <div class="input-group-append">
            <button class="btn btn-outline-secondary" @click="addCouponCode"></button>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="my-5 row justify-content-center">
      <form class="col-md-6" @submit.prevent="createOrder">
        <div class="form-group">
          <label for="useremail">Email</label>
          <input type="email" class="form-control" name="email" id="useremail"
            placeholder=" Email" v-validate="'required|email'" v-model="form.user.email">
          <span class="text-danger" v-if="errors.has('email')">
            <!--  -->
            {{errors.first("email")}}
          </span>
        </div>
      
        <div class="form-group">
          <label for="username"></label>
          <!-- is-invalid dangerinput -->
          <input type="text" class="form-control" name="name" id="username" :class="{'is-invalid':errors.has('name')}"
            placeholder=""  v-validate="'required'" v-model="form.user.name">
            <!-- v-if="errors.has('name')"  -->
          <span class="text-danger" v-if="errors.has('name')"></span>
        </div>
      
        <div class="form-group">
          <label for="usertel"></label>
          <input type="tel" class="form-control" id="usertel" placeholder="" name="phone"
          v-validate="'required'" :class="{'is-invalid':errors.has('phone')}" v-model="form.user.tel">
          <span class="text-danger" v-if="errors.has('phone')"></span>
        </div>
      
        <div class="form-group">
          <label for="useraddress"></label>
          <input type="text" class="form-control" name="address" id="useraddress" 
            placeholder="" v-model="form.user.address">
          <span class="text-danger"></span>
        </div>
      
        <div class="form-group">
          <label for="comment"></label>
          <textarea name="" id="comment" class="form-control" cols="30" rows="10"
          v-model="form.message"></textarea>
        </div>
        <div class="text-right">
          <button class="btn btn-danger"></button>
        </div>
      </form>
    </div>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  data(){
    return{
      isLoading: false,
      cupon_code: '',
      //
      cart: {},
      products:[],
      product:{},
      //itemloading
      status:{
        //id
        loadingItem: ''
      },
      form: {
        user: {},
      }
    }
  },
  methods: {
    getProducts(){
      const vm = this
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/products?page=:page`
      vm.isLoading = true
      this.$http.get(api).then((response)=>{
        console.log("",response)
        vm.products = response.data.products
        vm.isLoading = false
      })
    },
    //
    getProduct(id){
      const vm = this
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/product/${id}`
      vm.status.loadingItem = id
      this.$http.get(api).then((response)=>{
        vm.product = response.data.product
        console.log(response)
        $("#productModal").modal("show")
        //loadingItem.id item.id 
        vm.status.loadingItem = ''
      })
    },
    //
    addToCart(id,qty = 1){
      const vm = this
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`
      vm.status.loadingItem = id
      const cart = {
        product_id : id,
        qty
      }
      this.$http.post(api,{data: cart}).then((response)=>{
        console.log("",response)
        vm.status.loadingItem = ''
        vm.getCart()
        $("#productModal").modal("hide")
      })
    },
    //
    getCart(){
      const vm = this
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart`
      vm.isLoading = true
      this.$http.get(api).then((response)=>{
        
        console.log("",response)
        vm.cart = response.data.data
        vm.isLoading = false
      })     
    },
    removeCartItem(id){
      const vm = this
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/cart/${id}`
      vm.isLoading = true
      this.$http.delete(api).then((response)=>{
        console.log("",response)
        vm.getCart()
        vm.isLoading = false
      })          
    },
    addCouponCode(){
      const vm = this
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/coupon`
      const coupon = {
        code: vm.cupon_code
      }
      vm.isLoading = true
      this.$http.post(api,{data:coupon}).then((response)=>{
        console.log("",response)
        vm.getCart()
        vm.isLoading = false
      })         
    },
    createOrder(){
      const vm = this
      //
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order`
      const order = vm.form
      //
      this.$validator.validate().then((result) => {
        if (result) {
          this.$http.post(api,{data: order}).then((response)=>{
            console.log("",response)
            if(response.data.success){
              vm.$router.push(`/simulation/customer_checkout/${response.data.orderId}`)
            }
          })
        } else {
          console.log("")
        }
      });
    }
  },
  created(){
    this.getProducts()
    this.getCart()
  }
}
</script>

