//RangeError: Invalid time value"
// vueshop
<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="text-right mb-4">
      <button class="btn btn-primary" @click="openModal(true)"></button>
    </div>
    <table class="table">
      <thead>
        <tr>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"></th>
          <th scope="col"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="coupon in coupons" :key="coupon.id">
          <th scope="row">{{coupon.title}}</th>
          <td>{{coupon.percent}}</td>
          <td>{{coupon.due_date | date}}</td>
          <td>
            <span class="text-success" v-if="coupon.is_enabled"></span>
            <span v-else></span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm" @click="openModal(false,coupon)"></button>
            <button class="btn btn-outline-primary btn-sm" @click="openDelete(coupon)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <Switchpages :pages="pagination" @switch="getCoupon"></Switchpages>
    <!-- coupon -->
    <!-- v-modelcoupon? -->
    <!-- getCoupon -->
    <div class="modal fade" id="couponModal" tabindex="-1" role="dialog" 
      aria-labelledby="couponModalLabel" aria-hidden="true">
      <div class="modal-dialog modal-lg" role="document">
        <div class="modal-content border-0">
          <div class="modal-header bg-dark text-white">
            <h5 class="modal-title" id="couponModalLabel">
              <span v-if="isNew"></span>
              <span v-else>{{tempCoupon.title}}</span>
            </h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
            <div class="row">
              <div class="col-sm-12">
                <div>
                  <label for="title"></label>
                  <!-- form-control primaryoutline  -->
                  <input class="form-control mb-2" type="text" 
                  placeholder="" v-model="tempCoupon.title">
                </div>
                <!-- form-row input -->
                <!-- flexbox -->
                <div class="form-row">
                  <div class="col-md-6">
                    <label for="category"></label>
                    <input class="form-control" type="text"
                    placeholder="" v-model="tempCoupon.code">
                  </div>
                  <div class="col-md-6">
                    <label for="due_date"></label>
                    <!-- v-model -->
                    <input type="date" class="form-control mb-2" v-model="due_date">
                  </div>
                </div>
                <div class="form-row">
                  <div class="col-md-6">
                    <label for="">%</label>
                    <input class="form-control mb-4" type="text" 
                    placeholder="" v-model="tempCoupon.percent">
                  </div>
                </div>
                <div class="form-row">
                  <div class="col-md-6">
                    <label for=""></label>
                    <input type="checkbox" v-model="tempCoupon.is_enabled">
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-primary" @click="updateCoupon"></button>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="modal fade" id="deleteModal" tabindex="-1" role="dialog" aria-labelledby="exampleModalLabel" aria-hidden="true">
      <div class="modal-dialog" role="document">
        <div class="modal-content">
          <div class="modal-header bg-danger text-white">
            <h5 class="modal-title" id="exampleModalLabel"></h5>
            <button type="button" class="close" data-dismiss="modal" aria-label="Close">
              <span aria-hidden="true">&times;</span>
            </button>
          </div>
          <div class="modal-body">
             <strong class="text-danger">{{tempCoupon.title}}</strong> !
          </div>
          <div class="modal-footer">
            <button type="button" class="btn btn-secondary" data-dismiss="modal"></button>
            <button type="button" class="btn btn-danger" @click="openDeleteSure"></button>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from "jquery"
import Switchpages from "@/components/Switchpages"
import { mapGetters,mapActions } from 'vuex'
export default {
  components: {
    Switchpages
  },
  data(){
    return {
      coupons: [],
      pagination: {},
      tempCoupon: {},
      isNew: false,
      isLoading: false,
      due_date: new Date(),
    }
  },
  watch: {
    //timestamp
    due_date(){
      const vm = this
      //math.floor
      const timeStamp = Math.floor(new Date(vm.due_date)/1000)
      //timeStamp
      vm.tempCoupon.due_date = timeStamp
    }
  },
  methods: {
    getCoupon(page = 1){
      const vm = this
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupons?page=${page}`
      vm.isLoading = true
      this.$http.get(api).then((response)=>{
        console.log(response.data)
        vm.coupons = response.data.coupons
        vm.isLoading = false
        //
        vm.pagination = response.data.pagination
      })
    },
    openModal(isNew, item) {
      const vm = this;
      $('#couponModal').modal('show');
      vm.isNew = isNew;
      if (vm.isNew) {
        vm.tempCoupon = {};
      } else {
        vm.tempCoupon = Object.assign({}, item);
        //iso
        const dateAndTime = new Date(vm.tempCoupon.due_date * 1000).toISOString().split('T');
        vm.due_date = dateAndTime[0];
      }
    },
    updateCoupon(){
      const vm = this
      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon`
      let apiMethod = "post"
      vm.isLoading = true
      if(!vm.isNew){
        api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`
        apiMethod = "put"
      }
      this.$http[apiMethod](api,{data: vm.tempCoupon}).then((response)=>{
        console.log(response.data)
        vm.getCoupon()
        vm.isLoading = false
      })
      $("#couponModal").modal("hide")
    },
    openDelete(coupon){
      this.tempCoupon = coupon
      $("#deleteModal").modal("show")
    },
    openDeleteSure(){
      const vm = this
      const api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/coupon/${vm.tempCoupon.id}`
      this.$http.delete(api).then((response)=>{
        console.log(response.data)
        vm.getCoupon()
      })
      $("#deleteModal").modal("hide")
    }
  },
  created(){
    this.getCoupon()
  }
}
</script>